.item {
    border: 5px solid #000000;
    background-color : white;
    text-align: center;
    margin: 10px;
    width : 500px;
    height: 500px;
    flex-grow: 1;
    position: relative;
}


.image {
width : 100%;
height: 50%;
}


.eachItem {
   display:  inline-flex;
   flex-wrap : wrap;

}   

.title
{  
    white-space: nowrap;
}

.desc{
    background-color : rgb(255, 220, 220);
    color : rgba(39, 20, 20, 0.623);
    font-size: x-large;
    width : 100%;
    height : 27.6%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex ;
    justify-content: center;
    align-items: center;
}

